import React from 'react';
import s from './signUp.module.css'
import {Field, reduxForm} from 'redux-form';
import { Input } from '../common/formControles/formControler';
import { maxLengthCreator, required, minLengthCreator } from '../../Utils/Validators/validators';

let maxLength20 = maxLengthCreator(20);
let maxLength50 = maxLengthCreator(50);
let minLength6 = minLengthCreator(6);

const SignUpForm = (props) =>{
    return(
        <form className={s.form} onSubmit={props.handleSubmit} >
            <section className={s.input}>
                <span>Name</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength20]}  component={Input} name='firstName' type='text' placeholder='Your name is ...' />
                </section>
            </section >
            <section className={s.input}>
                <span >Surname</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength20]}  component={Input} name='lastName' type='text' placeholder='Your surname is ...' />
                </section>
            </section>
            <section className={s.input}>
                <span >Birthday</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required]}  component={Input} name='birthday' type='date'  />
                </section> 
            </section>
            <section className={s.input}>
                <span >Gender</span>
                <section className={s.Field}>
                    <section className={s.inline}>Male</section>
                    <section className={s.inline}>
                        <Field className={s.inline} component={Input} name='gender'  type='radio' value='male' />
                    </section>
                    <section className={s.inline}>Female</section>
                    <section className={s.inline}>
                        <Field className={s.inline} component={Input} name='gender' type='radio' value='female' />
                    </section>
                </section>
            </section>
            <section className={s.input}>
                <span >Country</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength20]}  component={Input} name='country' type='text' placeholder='Your country is ...' />
                </section>
            </section>
            <section className={s.input}>
                <span >City</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength20]}  component={Input} name='city' type='text' placeholder='Your city is ...' />
                </section>               
            </section>
            <section className={s.input}>
                <span >Email</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength50]}  component={Input} name='email' type='email' placeholder='Your email is ...' />
                </section>         
            </section>
            <section className={s.input}>
                <span >Password</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength20, minLength6]}  component={Input} name='password' type='password' placeholder='Your password is ...' />
                </section> 
            </section>
            <section className={s.input}>
                <span >Repeat Password</span>
                <section className={s.Field}>
                    <Field className={s.inputField} validate={[required, maxLength20, minLength6]}  component={Input} name='repassword' type='password' placeholder='Repeat your password ...' />
                </section>  
            </section>
            <section>
                <button className={s.btn}>Sign Up</button>
            </section>
        </form>
    )
}

const SignUpReduxForm = reduxForm({ form: 'signUp' })(SignUpForm)

const SignUp = (props) => {

    let signUpNewUs = (values) => {
        props.SetNewUser(values.firstName, values.lastName, values.birthday, values.gender, values.country, values.city, values.email, values.password, values.repassword)  
    }

    return(
        <section id={s.signUp}> 
            <span id={s.signUpSpan}>Sign Up</span>
            <SignUpReduxForm onSubmit={signUpNewUs} />
        </section>
        
    )
}


export default SignUp;