import React from 'react';
import SignUp from './signUp';
import { connect } from 'react-redux';
import { SetNewUser } from '../../redux/auth_reducer';
import { getMessageSelector } from '../../redux/signUp_selector';



class SignUpContainer extends React.Component{
    render(){
        return(
            <SignUp {...this.state} SetNewUser={this.props.SetNewUser} message={this.props.message} />
        )
    }
}

let mapStateToProps = (state) => {
    return{
        message: getMessageSelector(state)
    }
}



export default connect(mapStateToProps, {SetNewUser})(SignUpContainer);